<template>
    <div class="users-mng-box" @click="isClickOpearteBox">
        <div class="users-nav">
            <span class="id-span">{{ $t('setuser.username') }}</span><span class="name-span">{{
                $t('setuser.roleedit')
            }}</span>
            <span class="role-span" ref="editUsr">{{ $t('setuser.liveTime') }}</span>
            <span class="edit-span"></span>
        </div>
        <div class="mask-box">
            <div class="table-box">
                <div v-for="(item, i) in  users" :key="i" :class="{ 'users-item': true, 'active-user': actvUsr == i }"
                    @click="operateUsr(i)">
                    <span class="id-span">{{ item.user_name }}</span>
                    <span class="name-span">{{ item.group_name }}</span>
                    <span class="role-span">{{ $t('setuser.noLimit') }}</span>

                    <span class="edit-span" v-if="permision_level == 4">
                        <svg-loader @click="editUserDialog" v-show="actvUsr == i" class="edit-btn" icon="ActiveEditUser"
                            size="2.5rem"></svg-loader>
                        <svg-loader @click="delUser" v-show="actvUsr == i && this.$store.state.f_status.user!=item.user_name " class="trash-btn" icon="DelDmrct"
                            size="2.5rem"></svg-loader>
                    </span>
                </div>
                <div :class="{ 'users-item': true, 'add-line': true }">
                    <span class="id-span" v-if="permision_level == 4">
                        <LeetxButton :module="module" @click="addUserDialog" class="add-btn" :use-icon="true"
                            icon="StaticCircleAdd" bgcColor="transparent" height="2.5rem" width="2.5rem"
                            iconSize="2.5rem"></LeetxButton>
                    </span>
                    <span class="name-span"></span>
                    <span class="role-span"></span>
                    <span class="edit-span"></span>
                </div>
            </div>
        </div>

    </div>
</template>
<script>
import { authUserAddApi, authUserDelApi, authGroupLstApi, authUserLstApi, authUserModApi } from "../../api/authorize/authorize.js"
import userEdit from "../../dialogs/permission/edituser/edituser.js"
import userAdd from "../../dialogs/permission/adduser/adduser.js"
import cnfrmWin from '../../dialogs/common/confirm/cnfrm.js'
import { hasSameStructure } from '../../utils/deepCopy.js'
import logout from '../../utils/logout.js'
import { messageApi } from '../message/message.js'
import LeetxButton from "../controls/LeetxButton.vue"
export default {
    components: {
        LeetxButton
    },
    data() {
        return {
            module: "AUTH_GROUP",
            actvUsr: -1,
            users: [],
            groups: [],
            newUser: {
                user: "",
                pwd: "",
                user_key: "",
                workNum: "",
                group_id: "",
            },
            oldUser: {
                user_id: "",
                user_name: "",
                user_key: "",
                email: "",
                group_id: "",
                others: ""
            },
            isLogin: 0,
            userNames: []

        }
    },
    methods: {
        operateUsr(i) {
            this.actvUsr = this.actvUsr == i ? -1 : i
        },
        editUserDialog() {
            userEdit({ "groups": this.groups, "olduser": this.users[this.actvUsr], 'names': this.userNames }).then(
                async (result) => {
                    for (let key in this.oldUser) {
                        this.oldUser[key] = result.user[key].text
                    }
                    if (!hasSameStructure(this.oldUser, result.oldUser, true)) {
                        await this.editUser(this.oldUser)
                        setTimeout(() => {
                            if (result.olduser.user_name == this.$store.state.f_status.user) {
                                logout()
                                let success = this.$t('logout.success')
                                let loginAgain = this.$t('logout.loginAgain')
                                messageApi.success([success, loginAgain])
                            }
                        }, 500)
                    }

                })
        },
        addUserDialog() {
            if (this.groups.length <= 0) return
            userAdd({ 'groups': this.groups, 'names': this.userNames }).then((result) => {
                for (let key in this.newUser) {
                    this.newUser[key] = result[key].text
                }
                this.addUser(this.newUser)
            })
        },
        async addUser(newUser) {
            newUser = newUser ? newUser : { "user": `user${Math.random().toFixed(3) * 10000}`, "pwd": 123456, "group_id": 1 }
            await authUserAddApi(newUser).then((result) => {
                if (result.status != 0) {
                    messageApi.error(['添加用户失败'])
                } else {
                    this.getUserList()
                }
            }).catch(() => {
                // messgaeApi.error(['添加用户失败'])
            });
            this.getUserList()
        },
        delUser() {
            // let isDel = false
            // let name=this.users[this.actvUsr].user_name
            let user_id = this.users[this.actvUsr].user_id
            let title = this.$t('setuser.deluser')
            let confirmText = this.$t('setuser.yes')
            let cancelText = this.$t('setuser.no')
            let msg = this.$t('setuser.isDelGroup')
            cnfrmWin({ title, confirmText, cancelText, msg }).then(() => {

                authUserDelApi(user_id).then((result) => {
                    if (result.status != 0) {
                        messageApi.error(['删除用户失败'])
                    }
                }).catch(() => {
                    // messgaeApi.error(['删除用户失败'])        
                });
                this.getUserList()
                this.restActiveUser()
            })

        },
        getGroupList() {
            authGroupLstApi().then((result) => {
                if (result.status == 0) {
                    this.groups = result.data
                    this.initUserNames()
                } else {
                    this.groups = []
                }
            })
        },
        getUserList() {
            authUserLstApi().then((result) => {
                if (result.status == 0) {
                    this.users = result.data
                    this.initUserNames()
                } else {
                    this.users = []
                }
            })
        },
        async editUser(user) {
            await authUserModApi(user)
            this.getUserList()
        },
        restActiveUser() {
            this.actvUsr = -1
        },
        resetData() {
            this.actvUsr = -1
            this.users = []
            this.groups = []
        },
        initUserNames() {
            this.userNames = []
            for (let user of this.users) {
                this.userNames.push(user.user_name)
            }
        }
    },
    created() {
        if (this.permision_level >= 1) {
            this.getUserList()
            this.getGroupList()
        }
    },
    computed: {
        permision_level() {
            if (this.module in this.$store.state.f_status.permissions)
                return this.$store.state.f_status.permissions[this.module]
            else
                return 0
        },
    },
    // watch: {
    //     enabled: {
    //         immediate: true,
    //         // deep:true,
    //         handler() {
    //             if (this.isLogin != this.enabled) {
    //                 this.isLogin = this.enabled
    //                 if (this.enabled) {
    //                     this.getUserList()
    //                     this.getGroupList()
    //                 } else {
    //                     this.resetData()
    //                 }
    //             }

    //         }
    //     }
    //},
}
</script>
<style scoped>
li {
    list-style: none;
}

.users-mng-box {
    position: relative;
    height: 100%;
    left: 50%;
    transform: translateX(-50%);
    top: 1rem;
    font-size: var(--font1_5);
    width: 84.5vw;
    padding-bottom: 1rem;
    border-top: 2px solid var(--C00);
}

.users-nav {
    display: flex;
    width: 84.5vw;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    font-size: var(--font1_5);
    height: 3.9063vh;
    line-height: 3.9063vh;
    border-bottom: 1px solid var(--C00);
    color: var(--C65);

}

.mask-box {
    position: relative;
    width: 84.5vw;
    left: 50%;
    transform: translateX(-50%);
    height: 60vh;
    overflow: hidden;
}

.table-box {
    position: relative;
    width: calc(84.5vw + 18px);
    overflow-y: scroll;
    overflow-x: hidden;
    height: 100%;
}

.users-item {
    position: relative;
    left: 0;
    height: 3.9063vh;
    line-height: 3.9063vh;
    display: flex;
    width: 84.5vw;
    color: var(--C3B);
    border-bottom: 1px solid var(--Cd4);
    font-size: var(--font1_625);

}

.users-item span {
    position: relative;
}

.users-nav .id-span,
.users-item .id-span {
    /* flex: 1; */
    width: 30%;
}

.users-nav .name-span,
.users-item .name-span {
    /* flex: 2; */
    width: 30%;
}

.users-nav .role-span,
.users-item .role-span {
    /* flex: 2; */
    width: 15%;
}

.role-span {
    white-space: nowrap;
}

.users-nav .edit-span,
.users-item .edit-span {
    /* flex: 1; */
    width: 25%;
}

.users-item span:last-child {
    cursor: pointer;
}

.active-user {
    color: var(--C00c155) !important;
}

.add-btn {
    display: inline-block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.edit-btn {
    position: relative;
    right: 2.5rem;
    top: 50%;
    transform: translateY(-50%);
}

.trash-btn {
    position: relative;
    right: 1rem;
    top: 50%;
    transform: translateY(-50%);
}

.add-line {

    border-top: 2px solid var(--C00);
    border-bottom: 0px;
}
</style>