<template>
    <div class="adduser-box" v-if="isShow">
        <div class="adduser-title-line">
            <span class="adduser-title-msg">{{$t('newuser.newuser')}}</span>
            <span class="adduser-box-close"  @click="cancel">
                <svgloader class="icon-close" icon="Close" size="1.2rem"></svgloader>
            </span>
        </div>
        <div class="adduser-name-line">
            <div class="adduser-name-text">
                {{$t('newuser.username')}}
            </div>
            <InputText :imprprChrctr="illegalWords" :textValid="validNameType==-1" :validReason="nameReason" :text="''" :placeholder="$t('newuser.usernamemsg')" class="adduser-name-input" v-model="userData.user"></InputText>
        </div>
        <div class="adduser-pswrd-line">
            <div class="adduser-name-text">
                {{$t('newuser.initialPswrd')}}
            </div>
            <InputText :imprprChrctr="['space']" :textValid="validPswrdType==-1" :validReason="pwdReason" :placeholder="$t('newuser.pswrdmsg')" class="adduser-pswrd-input" :type="pswdOrtext" v-model="userData.pwd"></InputText>
            <svgloader :icon="eyeType" class="user-pswd-eye" size="1.5rem" v-show="haseye" @click="showPswrd"></svgloader>
        </div>

        <!-- <div class="adduser-worknum-line">
            <div class="adduser-worknum-text">
                {{$t('newuser.worknumber')}}
            </div>
            <InputVal class="adduser-worknum-input" :sensorValue="0" :isInt="true" :isLimit="false"  @updateData="userData.workNum=$event"></InputVal>
            <InputText :text="''" fntFmly="--R-M"  :placeholder="$t('newuser.worknumbermsg')" class="adduser-worknum-input"  v-model="userData.workNum"></InputText> -->
        <!-- </div> -->


        <div class="adduser-worknum-line">
            <div class="adduser-worknum-text">
                登录码
            </div>
            <!-- <InputVal class="adduser-worknum-input" :sensorValue="0" :isInt="true" :isLimit="false"  @updateData="userData.workNum=$event"></InputVal>-->
            <InputText :text="''" fntFmly="--R-M" :isShowKeyBoard = false placeholder="请刷卡注册" class="adduser-worknum-input"  v-model="userData.workNum"></InputText> 
        </div>
        <div class="adduser-role-line">
            <div class="adduser-role-text">
                 {{$t('newuser.prmsnrole')}}
            </div>
            <NewSelect class="adduser-name-input" :width="17.625" :realVal="userData.group_id.text" :options="getGroups()" @valChng="userData.group_id.text=$event"></NewSelect>
        </div>
        <div class="adduser-operate-line">
            <div class="adduser-cancel-btn" @click="cancel">{{$t('dlgadduser.cancel')}}</div>
            <div v-if="groups.length!=0 && allowAddUser" class="adduser-cnfrm-btn" @click="sbmtUserInfo" v-show="groups.length!=0">{{$t('dlgadduser.sure')}}</div>
        </div>
    </div>
</template>
<script>
import svgloader from '../../../components/icon_font/svgloader.vue'

import InputText from '../../../components/input/InputText.vue'
// import InputVal from '../../../components/input/InputVal.vue'
import NewSelect from '../../../components/select/NewSelect.vue'
import { maskApi } from '../../mask/StackMask.vue'

export default {
    components:{InputText,svgloader,NewSelect,
                // InputVal
                },
    data() {
        return {
            userData:{
                user:{
                    text:"",
                    valid:false,
                },
                pwd:{
                    text:"",
                    valid:false,
                },
                workNum:{
                    text:0,
                    valid:false,
                },
                user_key:{
                    text:"",
                    valid:false,
                },
                group_id:{
                    text:this.groups[0].group_id,
                    valid:true,
                },
            },
            isShow:true,
            isShowPswrd:false,
            illegalWords:[',','.',':',';',"'",'"','?',"!","-","+","/","=",'(',")",'space'],
            validNameType:1,
            validWorkerNumType:1,
            validPswrdType:1,
            maskId:0,
        }
    },
    methods: {
        showPswrd(){
            this.isShowPswrd=!this.isShowPswrd
        },
        sbmtUserInfo(){
            this.isShow=false
            this.onConfirm && this.onConfirm(this.userData)
            maskApi.descend(this.maskId)
        },
        cancel(){
            this.isShow=false
            this.onCancel && this.onCancel()
            maskApi.descend(this.maskId)
        },
        getGroups(){
            let obj={}
            for(let key in this.groups){
                obj[this.groups[key].group_name]=this.groups[key].group_id
            }
            return obj
        },
        initStatus(){
            this.maskId=maskApi.rise(15,this.cancel)
        },
        updateWorkNumValidType(){
            if(this.userData.workNum==""){
                this.validWorkerNumType=1
            }else{
                this.validWorkerNumType=-1
            }
        },
        updateUserValidType(){
            if(this.userData.user.text==""){
                this.validNameType=1
            }
            else if(this.names.indexOf(this.userData.user.text)!=-1){
                this.validNameType=2
            }
            else{
                this.validNameType=-1
            }
        },
        updatePswrdValidType(){
            if(this.userData.pwd.text==""){
                this.validPswrdType=1
            }else{
                this.validPswrdType=-1
            }
        },

    },
    props:{
        groups:{
            type:Object,
            default:function(){
                return {}
            }
        },
        names:{
            type:Array,
            default:function(){
                return []
            }
        }
    },
    watch:{
        groups:{
            deep:true,
            handler:function(){
                (this.groups);
            }
        },
        'userData.user':{
            deep:true,
            immediate:true,
            handler:function(){
                this.updateUserValidType()
            }
        },
        'userData.pwd':{
            deep:true,
            immediate:true,
            handler:function(){
                this.updatePswrdValidType()
            }
        },
        'userData.workNum':{
            deep:true,
            immediate:true,
            handler:function(){
                this.updateWorkNumValidType()
            }
        },
        names:{
            deep:true,
            handler:function(){
                // this.updateValidType()
                this.updateUserValidType()
            }
        }
        
    },
    computed:{
        pswdOrtext(){
            if(this.isShowPswrd){
                return 'text'
            }
            return 'password'
        },
        haseye(){
            return this.userData.pwd.text.length>0
        },
        eyeType(){
            return this.isShowPswrd?"OpenEye":"CloseEye"
        },
        nameReason(){
            if(this.validNameType==-1){
                return ""
            }
            if(this.validNameType==1){
                return this.$t('dlgadduser.nameReasonOne')
            }
            if(this.validNameType==2){
                return this.$t('dlgadduser.nameReasonTwo')
            }
            if(this.validNameType==3){
                return this.$t('dlgadduser.nameReasonThree')
            }
            return ""
        },
        pwdReason(){
            if(this.validPswrdType==-1){
                return ""
            }
            if(this.validPswrdType==1){
                return this.$t('dlgadduser.pwdReasonOne')
            }
            if(this.validPswrdType==2){
                return this.$t('dlgadduser.pwdReasonTwo')
            }
            return ""
        },
        numReason(){
            if(this.validWorkerNumType==1){
                return ""
            }
            if(this.validWorkerNumType==1){
                return this.$t('dlgadduser.numReasonOne')
            }
            return ""
        },
        allowAddUser(){
            let valid=true
            valid=valid && (this.validNameType==-1)
            valid=valid && (this.validPswrdType==-1)
            valid=valid && (this.validWorkerNumType==-1)
            return valid
        }
    },
    mounted() {
        this.initStatus()
    },
}
</script>
<style scoped>
.adduser-box{
    width: 37.5rem;
    height: 31.25rem;
    background-color: var(--fff);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    border-radius: 0.375rem;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    z-index: 40;
}
.adduser-title-line{
    position: absolute;
    top: 0;
    width: 100%;
    height: 3.2688rem;
    background-color: var(--Cee);
    color: var(--C3B);
    border-radius: 0.375rem;;
}
.adduser-title-msg{
    position: absolute;
    display: inline-block;
    left: 50%;
    transform: translate(-50%,-50%);
    top: 50%;
    height: 2.4375rem;
    font-size:var(--font1_75);
    line-height: 2.4375rem;
    letter-spacing: 0px;
    color: var(--C3B);
}
.adduser-box-close{
    display: inline-block;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 3rem;
    height: 3rem;
    color: var(--C65);
}
.icon-close{
    position: absolute;
    right: 1.0625rem;
    top: 50%;
    transform: translateY(-50%);
}
.adduser-name-line{
    position: absolute;
    width: 100%;
    top: 5.8469rem;
    height: 3.125rem;
}
.adduser-pswrd-line{
    position: absolute;
    width: 100%;
    top: 10.2844rem;
    height: 3.125rem;
}
.adduser-worknum-line{
    position: absolute;
    width: 100%;
    top: 14.7219rem;
    height: 3.125rem;
}
.adduser-role-line{
    position: absolute;
    width: 100%;
    top: 19.1594rem;
    height: 3.125rem;
}
.adduser-name-text,.adduser-role-text,.adduser-worknum-text{
    position: absolute;
    left: 3.23rem;
    top: 50%;
    transform: translateY(-50%);
    height:1.5625rem;
    font-size:var(--font1_625);
    line-height:1.5625rem;
    color: var(--C3B);
}
.adduser-name-input,.adduser-pswrd-input{
    position: absolute;
    height: 100%;
    width:17.625rem;
    text-align: center;
    font-size:var(--font1_625);
    right: 3.2106rem;
    border-radius: 0.25rem;
    color: var(--C3B);
    background-color: var(--fff);
    border: 1px solid  var(--Cf1);
    box-sizing: border-box;
}
.adduser-worknum-input{
    position: absolute;
    right: 3.2106rem;

}
.adduser-operate-line{
        position: absolute;
        width: 100%;
        bottom: 3.15rem;
        height: 3.125rem;
}
.adduser-cnfrm-btn{
    text-align: center;
    position: absolute;
    top:0;
    height:  3.125rem;
    line-height:  3.125rem;
    width: 8.625rem;
    right: 8.4375rem;
    background-color: var(--C00c155);
    font-size:var(--font1_625);
    color: var(--fff);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    border-radius:0.25rem ;
}
.adduser-cancel-btn{
    text-align: center;
    position: absolute;
    top:0;
    height:3.125rem;
    line-height:3.125rem;
    width: 8.625rem;
    left: 8.4375rem;
    background-color: var(--Cf1);
    font-size:var(--font1_625);
    color: var(--C00);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    border-radius:0.25rem ;
}
.user-pswd-eye{
    display: inline-block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 82.88%;
    z-index: 33;
}
</style>