// 递归实现深拷贝

export function deepCopy(obj) {
    let ctn
    if (obj instanceof Array) {
        ctn = []
        for (let i = 0; i < obj.length; i++) {
            ctn[i] = deepCopy(obj[i])
        }
    }
    else if (obj instanceof Object) {
        ctn = {}
        for (let key of Object.keys(obj)) {
            ctn[key] = deepCopy(obj[key])
        }
    }
    else {
        ctn = obj
    }
    return ctn
}
// 给工艺表单初始化数据,实际上组件传出数据的时候,外层只用到了sensorValue
export function initData(param) {
    if (param instanceof Array) {
        throw new Error(`${param} 不能为数组  `)
    }
    let val
    if (param instanceof Object) {
        val = {}
        for (let key in param) {
            val[key] = initData(param[key])
        }
    }
    else {
        val = { sensorValue: param, displayValue: 0, valid: true }
    }
    return val
}


export function getDeepData(current, target, fn) {
    if (target instanceof Object) {
        for (let key in target) {
            target[key] = getDeepData(current[key], target[key], fn)
        }
    } else {
        target = current.sensorValue
        fn && fn(target, current)
    }
    return target
}

// 判断两个对象的数据结构是否一样,值可以不一样
// 深度遍历
function sameStructure(obj1, obj2, isCheckValSame = false) {
    let isSame = true
    // 同为数组怎么样处理
    if ((obj1 instanceof Array) && (obj2 instanceof Array) && (obj2.length == obj1.length)) {
        for (let i = 0; i < obj1.length; i++) {
            isSame = isSame && sameStructure(obj1[i], obj2[i], isCheckValSame)
            if (!isSame) {
                break
            }
        }
    }
    // 同为对象但不是数组怎么样处理
    else if ((obj1 instanceof Object) && (obj2 instanceof Object) && !(obj1 instanceof Array) && !(obj2 instanceof Array)) {
        for (let key in obj1) {
            // obj2里面有这个属性
            if (key in obj2) {
                isSame = isSame && sameStructure(obj1[key], obj2[key], isCheckValSame)
                if (!isSame) {
                    break
                }
            } else {
                isSame = false
                break
            }
        }
    }
    else if (!(obj1 instanceof Object) && !(obj2 instanceof Object)) {
        // 说明数据结构一样
        isSame = true
        // 检测值是不是一样的
        if (isCheckValSame) {
            isSame = obj1 === obj2
        }
    }
    else {
        isSame = false
    }
    return isSame
}

