//两个数据结构是否相同
export function hasSameStructure(obj1, obj2, isCheckValSame = false) {
    if (isCheckValSame) {
        return JSON.stringify(obj1) === JSON.stringify(obj2)
    }
    return sameStructure(obj1, obj2)
}



//检测对象是否包含空值
export function checkObjHasNull(obj, replaceVal) {
    if (obj === null || obj === undefined) return true
    if (typeof (obj) != 'object') return false
    for (let key in obj) {
        const isNull = checkObjHasNull(obj[key])
        if (isNull) {
            obj[key] = replaceVal
        }
    }
    return false
}





