import { createApp } from "vue";
import AddUser from '../../../dialogs/permission/adduser/AddUser.vue'
import langvue from "../../../plugins/langVue";
// 导出函数可通过调用 Confirm() 函数动态创建 XtxConfirm 组件
const userAdd = (props={}) => {
    if(typeof(props)!="object") throw new Error("props must be a object")
    return new Promise((resolve, reject) => { //promise封装，ok执行resolve，no执行reject
        // 创建组件实例
        (props);
        let dom = createApp(AddUser,props).use(langvue).mount(document.createElement("div"));
        // 添加元素
        document.body.appendChild(dom.$el);
        // 重写组件的函数
        dom.onConfirm = function (x) {
            resolve(x)
        }
        dom.onCancel = function (err) {
            reject(err)
        }
    })
}
export default userAdd