<template>
    <div class="eidtuser-box" v-if="isShow">
        <div class="eidtuser-title-line">
            <span class="eidtuser-title-msg">{{$t('dlgedituser.editUser')}}</span>
            <span class="edituser-box-close"  @click="cancel">
                <svgloader class="icon-close" icon="Close" size="1.2rem"></svgloader>
            </span>
        </div>
        <div class="eidtuser-name-line">
            <div class="eidtuser-name-text">
                {{$t('dlgedituser.name')}}
            </div>
            <InputText :text="olduser.user_name" :imprprChrctr="['space']" :textValid="validNameType==-1" :validReason="nameReason"  class="eidtuser-name-input"   v-model="user.user_name"></InputText>
        </div>
        <div class="eidtuser-role-line">
            <div class="eidtuser-role-text">
                {{$t('dlgedituser.newRole')}}
            </div>
            <NewSelect class="eidtuser-name-input" :width="18.4768" :realVal="user.group_id.text" @valChng="user.group_id.text=$event" :options="groupInfo"></NewSelect>
        </div>
        <div class="eidtuser-key-line">
            <div class="eidtuser-key-text">
                登录码
            </div>
            <!-- <InputVal class="adduser-worknum-input" :sensorValue="0" :isInt="true" :isLimit="false"  @updateData="userData.workNum=$event"></InputVal>-->
            <InputText :text="''" fntFmly="--R-M" :isShowKeyBoard = false placeholder="请刷卡注册" class="eidtuser-key-input"  v-model="user.user_key"></InputText> 
        </div>
        <br>
        <div class="edituser-operate-line">
            <div class="edituser-cancel-btn" @click="cancel">
                {{$t('dlgedituser.cancel')}}
            </div>
            <div class="edituser-cnfrm-btn" @click="sbmtUserInfo" v-show="groups.length!=0 && validNameType==-1">
                {{$t('dlgedituser.confirm')}}
            </div>
        </div>
    </div>
</template>
<script>
import InputText from '../../../components/input/InputText.vue'
import svgloader from '../../../components/icon_font/svgloader.vue'
import NewSelect from '../../../components/select/NewSelect.vue'
import { maskApi } from '../../mask/StackMask.vue'
export default {
    components:{InputText,svgloader,
    NewSelect
    },
    data() {
        return {
            user:{
                user_id:{
                    text:"",
                    valid:false
                },
                user_name:{
                    text:"",
                    valid:false
                },
                email:{
                    text:"",
                    valid:false
                },
                group_id:{
                    text:"",
                    valid:false
                },
                user_key:{
                    text:"",
                    valid:false,
                },
                others:{
                    text:"",
                    valid:false
                }
            },
            groupInfo:{},
            isShow:true,
            validNameType:-1,
            maskId:0,
        }
    },
    methods: {
        sbmtUserInfo(){
            this.isShow=false
            let user=this.user
            let olduser=this.olduser
            this.onConfirm && this.onConfirm({user,olduser})
            maskApi.descend(this.maskId)
        },
        cancel(){
            this.isShow=false
            this.onCancel && this.onCancel()
            maskApi.descend(this.maskId)
        },
        initGroupInfo(){
            for(let i=0;i<this.groups.length;i++){
                this.groupInfo[this.groups[i].group_name]=this.groups[i].group_id
            }
        },
        initStatus(){
            this.maskId=maskApi.rise(15,this.cancel)
        },
        updateUserValidType(){
            if(this.user.user_name.text==""){
                this.validNameType=1
            }
            else if(this.olduser.user_name!=this.user.user_name.text && this.names.indexOf(this.user.user_name.text)!=-1  ){
                this.validNameType=2
            }
            else if(this.user.user_name.text.length>10){
                this.validNameType=3
            }
            else{
                this.validNameType=-1
            }
        },
    },
    props:{
        groups:{
            type:Object,
            default:function(){
                return {}
            }
        },
        olduser:{
            type:Object,
            default:function(){
                return {}
            }
        },
        names:{
            type:Array,
            default:function(){
                return []
            }
        }
    },
    watch:{
        olduser:{
            immediate:true,
            deep:true,
            handler:function(){
                for(let key in this.user){
                    this.user[key].text=this.olduser[key]
                }
            }
        },
        groups:{
            immediate:true,
            deep:true,
            handler:function(){
                this.initGroupInfo()
            }
        },
        'user.user_name':{
            deep:true,
            immediate:true,
            handler:function(){
                this.updateUserValidType()
            }
        },
    },
    computed:{
        nameReason(){
            if(this.validNameType==-1){
                return ""
            }
            if(this.validNameType==1){
                return this.$t('dlgadduser.nameReasonOne')
            }
            if(this.validNameType==2){
                return this.$t('dlgadduser.nameReasonTwo')
            }
            if(this.validNameType==3){
                return this.$t('dlgadduser.nameReasonThree')
            }
            return ""
        },
    },
    mounted() {
        this.initStatus()
    },
}
</script>
<style scoped>
.eidtuser-box{
    width: 37.5rem;
    height: 31.25rem;
    background-color: var(--fff);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    border-radius: 0.375rem;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    z-index: 40;
}
.eidtuser-title-line{
    position: absolute;
    top: 0;
    width: 100%;
    height: 3.2688rem;
    background-color: var(--Cee);
    color: var(--C3B);
    border-radius: 0.375rem;;
}
.eidtuser-title-msg{
    position: absolute;
    display: inline-block;
    left: 50%;
    transform: translate(-50%,-50%);
    top: 50%;
    height: 2.4375rem;
    font-size:var(--font1_75);
    line-height: 2.4375rem;
    letter-spacing: 0px;
    
    color: var(--C3B);
}
.edituser-box-close{
    display: inline-block;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 3rem;
    height: 3rem;
    color: var(--C65);
}
.icon-close{
    position: absolute;
    right: 1.0625rem;
    top: 50%;
    transform: translateY(-50%);
}
.eidtuser-name-line{
    position: absolute;
    width: 100%;
    top: 9.6875rem;
    height: 3.125rem;
}
.eidtuser-role-line{
    position: absolute;
    width: 100%;
    top: 14.125rem;
    height: 3.125rem;
}

.eidtuser-key-line{
    position: absolute;
    width: 100%;
    top: 18.5625rem;
    height: 3.125rem;
}



.eidtuser-name-text,.eidtuser-role-text,.eidtuser-key-text{
    position: absolute;
    left: 3.23rem;
    top: 50%;
    transform: translateY(-50%);
    height:1.5625rem;
    
    font-size:var(--font1_625);
    line-height:1.5625rem;
    color: var(--C3B);
}
.eidtuser-name-input{
    position: absolute;
    height: 100%;
    width:18.4768rem!important;
    text-align: center;
    font-size:var(--font1_625);
    right: 3.2106rem;
    border-radius: 0.25rem;
    color: var(--C3B);
    background-color: var(--fff);
    border: 1px solid  var(--Cf1);
    box-sizing: border-box;
}

.eidtuser-key-input{
    position: absolute;
    height: 100%;
    width:18.4768rem!important;
    text-align: center;
    font-size:var(--font1_625);
    right: 3.2106rem;
    border-radius: 0.25rem;
    color: var(--C3B);
    background-color: var(--fff);
    border: 1px solid  var(--Cf1);
    box-sizing: border-box;
}
.edituser-operate-line{
    position: absolute;
    width: 100%;
