}
.edituser-cnfrm-btn{
    text-align: center;
    position: absolute;
    top:0;
    height:  3.125rem;
    line-height:  3.125rem;
    width: 8.625rem;
    right: 8.4375rem;
    background-color:var(--C00c155);
    font-size:var(--font1_625);
    color: var(--fff);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    border-radius:0.25rem ;
    
}
.edituser-cancel-btn{
    text-align: center;
    position: absolute;
    top:0;
    height:3.125rem;
    line-height:3.125rem;
    width: 8.625rem;
    left: 8.4375rem;
    background-color:  var(--Cee);
    font-size:var(--font1_625);
    color: var(--C00);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    border-radius:0.25rem ;
    
}
</style>